import React, { Component } from 'react';
import Board from './board.js'
import './App.css';
import NewGame from './newGame.js';

class App extends Component {
  constructor() {
    super();
    this.state = {
      board: [], avail: new Array(9).fill(new Array(9)), selected: { i: -1, j: -1 },
      fixedAvail: new Array(9).fill(new Array(9)),
      boardClass: new Array(9),
      boardText: new Array(9).fill(new Array(9)),
      rule: "", tab: "none", fixedBoard: [], remove: false,
      canRemove: [], screen: "Menu", type: "saved", AN: true, Strategies: true,
      difficulty: "Easy", strategies: null
    };
  };
  setScreen(screen) {
    this.setState({ screen });
  }
  clear(keepSelected) {
    let boardClass = this.state.boardClass;
    let boardText = this.state.boardText;
    let selected = this.state.selected;
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        boardClass[i][j] = [];
        boardText[i][j] = "";
      }
    }
    if (keepSelected !== false && this.state.selected.i !== -1) {
      selected = { i: -1, j: -1 };
    }
    this.setState({ boardClass, boardText, selected });
  }
  erase() {
    let board = this.state.board;
    board[this.state.selected.i][this.state.selected.j] = this.state.fixedBoard[this.state.selected.i][this.state.selected.j];
    this.setState({ board, selected: { i: -1, j: -1 } }, () => { this.allavail() })
  }
  SetState(state, fun = () => { }) {
    this.setState(state, fun);
  }
  reset() {
    let board = [];
    this.state.fixedBoard.forEach(array => {
      board.push([...array]);
    })
    this.setState({
      board,
      removed: [],
      rule: ""
    });
  }

  renderWin() {
    return (
      <div key="Win" className="d-flex align-items-center h-100" style={{ "min-height": "100vh", "min-width": "100vw" }}>
        <div className="d-flex justify-content-center " style={{ "min-width": "100vw" }}>
          <div className="d-flex flex-column ">
            You Win!!!
         <button onClick={() => { this.reset(); this.setState({ screen: "Menu" }) }} className="btn-sm mr-2" >New Game</button>
          </div>
        </div>
      </div>
    )
  }
  render() {
    let output
    if (this.state.screen === "Board") output = <Board
      board={this.state.board}
      avail={this.state.avail}
      selected={this.state.selected}
      fixedAvail={this.state.fixedAvail}
      fixedBoard={this.state.fixedBoard}
      boardClass={this.state.boardClass}
      boardText={this.state.boardText}
      rule={this.state.rule}
      tab={this.state.tab}
      remove={this.state.remove}
      removed={this.state.removed}
      canRemove={this.state.canRemove}
      type={this.state.type}
      AN={this.state.AN}
      Strategies={this.state.Strategies}
      difficulty={this.state.difficulty}
      setRule={this.setRule}
      setScreen={this.setScreen.bind(this)}
      SetState={this.SetState.bind(this)}
      allavail={this.allavail}
      singles={this.singles}
      uniques={this.uniques}
      subsets={this.subsets}
      clear={this.clear.bind(this)}
      hiddenSubsets={this.hiddenSubsets}
    ></Board>
    if (this.state.screen === "Menu") output = <NewGame
      type={this.state.type}
      board={this.state.board}
      boardClass={this.state.boardClass}
      boardText={this.state.boardText}
      difficulty={this.state.difficulty}
      AN={this.state.AN}
      Strategies={this.state.Strategies}
      SetState={this.setState.bind(this)}
      reset={this.reset.bind(this)}
      clear={this.clear.bind(this)}
    ></NewGame>
    if (this.state.screen === "Win") output = this.renderWin()
    return (
      <div>
        {output}
      </div>

    )
  }
}
export default App;