import React, { Component } from 'react';

export default class Strategies extends Component {
  xwingHelper(number,removefrom, seta, setb, boardClass, boardText, ) {     
    let canRemove = []; 
    seta.forEach((cell)=>{
      boardClass[cell.i][cell.j].push("XWing");  
      boardText[cell.i][cell.j] = number  
    })
    setb.forEach((cell)=>{
      boardClass[cell.i][cell.j].push("XWing");    
      boardText[cell.i][cell.j] = number
    })
    removefrom.forEach((cell)=>{
      if(cell.i === seta[0].i && cell.j === seta[0].j) return;
      if(cell.i === seta[1].i && cell.j === seta[1].j) return;
      if(cell.i === setb[0].i && cell.j === setb[0].j) return;
      if(cell.i === setb[1].i && cell.j === setb[1].j) return;
      if(this.props.avail[cell.i][cell.j].includes(number)){
        boardText[cell.i][cell.j] = "avail";
        boardClass[cell.i][cell.j].push("canRemove")
        canRemove.push({i:cell.i,j:cell.j, value:number})
      }
    })
    return {boardClass,boardText,canRemove};
  }
  xwing() {
    let boardClass = this.props.boardClass;
    let boardText = this.props.boardText;
    let canRemove = [];
    let numbers = [];
    for (let i = 0; i < 9; i++) {      
      let column = [];
      for (let j = 0; j < 9; j++) {
        column.push({ i, j })
      }
      numbers[i] = new Array(10);
      for(let j = 0;j<10;j++){
        numbers[i][j]= []
      }
      for (let index = 0; index < 9; index++) {
        let avail = this.props.avail[column[index].i][column[index].j]
        if (avail.length === 0) continue;
        if (this.props.board > 0) continue;
        avail.forEach((number) => {
          numbers[i][number].push({ i: column[index].i, j: column[index].j })
        })
      }
    }
    for (let number = 1; number < 10; number++) {
      for (let index = 0; index < 9; index++) {
        let seta =numbers[index][number]
        if(seta.length !== 2) continue;  
        for (let indexb = index +1; indexb < 9; indexb++) {
          let setb = numbers[indexb][number]
          if(setb.length !== 2) continue;
          if(seta[0].j !== setb[0].j) continue;
          if(seta[1].j !== setb[1].j) continue;
          let removefrom = [];
          for(let i=0;i<9;i++){
            removefrom.push({i,j:seta[0].j})
            removefrom.push({i,j:seta[1].j})
          }
          let r = this.xwingHelper(number,removefrom,seta,setb,boardClass,boardText)
          r.canRemove.forEach((e)=>{canRemove.push(e)});
          boardClass = r.boardClass;
          boardText = r.boardText;
        }
      }
    }
    for (let j = 0; j < 9; j++) {      
      numbers.push([]);      
      let row = [];
      for (let i = 0; i < 9; i++) {
        row.push({ i, j })
      }
      numbers[j] = new Array(10);
      for(let i = 0;i<10;i++){
        numbers[j][i]= []
      }
      for (let index = 0; index < 9; index++) {
        let avail = this.props.avail[row[index].i][row[index].j]        
        if (avail.length === 0) continue;
        if (this.props.board > 0) continue;
        
        avail.forEach((number) => {
          numbers[j][number].push({ i: row[index].i, j: row[index].j})
        })
      }
    }
    for (let number = 1; number < 10; number++) {
      for (let index = 0; index < 9; index++) {
        let seta =numbers[index][number]        
        if(seta.length !== 2) continue;             
        for (let indexb = index + 1; indexb < 9; indexb++) {
          let setb = numbers[indexb][number]
          if(setb.length !== 2) continue;
          if(seta[0].i !== setb[0].i) continue;
          if(seta[1].i !== setb[1].i) continue;
          let removefrom = [];
          for(let j=0;j<9;j++){
            removefrom.push({j,i:seta[0].i})
            removefrom.push({j,i:seta[1].i})
          }
          let r = this.xwingHelper(number,removefrom,seta,setb,boardClass,boardText)
          r.canRemove.forEach((e)=>{canRemove.push(e)});
          boardClass = r.boardClass;
          boardText = r.boardText;
        }
      }
    }
    this.props.SetState({boardClass,boardText,canRemove})
  }



  singles() {
    let boardClass = this.props.boardClass
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        if (this.props.avail[i][j].length === 1) {
          boardClass[i][j].push("single");
        }
      }
    }
    this.props.SetState({ boardClass });
  }
  hiddenSubsets2helper(k, numbers, boardClassText) {
    let boardClass = boardClassText.boardClass;
    let boardText = boardClassText.boardText;
    let canRemove = [];
    let skip = false;
    if (numbers[k].length > 1 && numbers[k].length < 3) {
      for (let l = k + 1; l < 10; l++) {
        if (numbers[l].length > 1 && numbers[l].length < 3) {
          let temp = this.sumArray(numbers[k], numbers[l]);
          if (temp.length === 2) {
            this.props.avail[temp[0].i][temp[0].j].forEach((e) => {
              if (e !== k && e !== l) canRemove.push({ i: temp[0].i, j: temp[0].j, value: e })
            })
            this.props.avail[temp[1].i][temp[1].j].forEach((e) => {
              if (e !== k && e !== l) canRemove.push({ i: temp[1].i, j: temp[1].j, value: e })
            })
            if (canRemove.length > 0) {
              boardClass[temp[0].i][temp[0].j].push("hiddensubset2")
              boardText[temp[0].i][temp[0].j] = this.props.avail[temp[0].i][temp[0].j].map((entry, index) => {
                return <span>{(index > 0 ? "," : "")}<span className={(canRemove.filter((e) => (e.i === temp[0].i && e.j === temp[0].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
              });

              boardClass[temp[1].i][temp[1].j].push("hiddensubset2")
              boardText[temp[1].i][temp[1].j] = this.props.avail[temp[1].i][temp[1].j].map((entry, index) => (
                <span>{(index > 0 ? "," : "")}<span className={(canRemove.filter((e) => (e.i === temp[1].i && e.j === temp[1].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
              ));
            }
            skip = true; break;
          }
        }
      }
      if (skip === true) return { boardClass, boardText };
    }
    return { boardClass, boardText, canRemove }
  }
  hiddenSubsets3helper(k, numbers, boardClassText) {
    let boardClass = boardClassText.boardClass;
    let boardText = boardClassText.boardText;
    let canRemove = [];
    if (numbers[k].length > 1 && numbers[k].length < 4) {
      for (let l = k + 1; l < 10; l++) {
        if (numbers[l].length > 1 && numbers[l].length < 4) {
          let temp = this.sumArray(numbers[k], numbers[l]);
          if (temp.length < 4) {
            for (let m = l + 1; m < 10; m++) {
              if (numbers[m].length > 1 && numbers[m].length < 4) {
                let temp = this.sumArray(numbers[k], numbers[l], numbers[m]);
                if (temp.length === 3) {
                  this.props.avail[temp[0].i][temp[0].j].forEach((e) => {
                    if (e !== k && e !== l && e !== m) canRemove.push({ i: temp[0].i, j: temp[0].j, value: e })
                  })
                  this.props.avail[temp[1].i][temp[1].j].forEach((e) => {
                    if (e !== k && e !== l && e !== m) canRemove.push({ i: temp[1].i, j: temp[1].j, value: e })
                  })
                  this.props.avail[temp[2].i][temp[2].j].forEach((e) => {
                    if (e !== k && e !== l && e !== m) canRemove.push({ i: temp[2].i, j: temp[2].j, value: e })
                  })
                  if (canRemove.length > 0) {
                    boardClass[temp[0].i][temp[0].j].push("hiddensubset3")
                    boardText[temp[0].i][temp[0].j] = this.props.avail[temp[0].i][temp[0].j].map((entry, index) => {
                      if (index % 3 === 0 && index !== 0) {
                        return <span><br /><span className={(canRemove.filter((e) => (e.i === temp[0].i && e.j === temp[0].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                      }
                      return <span>{(index > 0 ? "," : "")}<span className={(canRemove.filter((e) => (e.i === temp[0].i && e.j === temp[0].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                    });

                    boardClass[temp[1].i][temp[1].j].push("hiddensubset3")
                    boardText[temp[1].i][temp[1].j] = this.props.avail[temp[1].i][temp[1].j].map((entry, index) => {
                      if (index % 3 === 0 && index !== 0) {
                        return <span><br /><span className={(canRemove.filter((e) => (e.i === temp[1].i && e.j === temp[1].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                      }
                      return <span>{(index > 0 ? "," : "")}<span className={(canRemove.filter((e) => (e.i === temp[1].i && e.j === temp[1].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                    });
                    boardClass[temp[2].i][temp[2].j].push("hiddensubset3")
                    boardText[temp[2].i][temp[2].j] = this.props.avail[temp[2].i][temp[2].j].map((entry, index) => {
                      if (index % 3 === 0 && index !== 0) {
                        return <span><br /><span className={(canRemove.filter((e) => (e.i === temp[2].i && e.j === temp[2].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                      }
                      return <span>{(index > 0 ? "," : "")}<span className={(canRemove.filter((e) => (e.i === temp[2].i && e.j === temp[2].j && e.value * 1 === 1 * entry)).length > 0 ? "text-danger" : "")}>{entry}</span></span>
                    });
                  }
                }
              }
            }
          }
        }
      }
    }
    return { boardClass, boardText, canRemove }
  }
  hiddenSubsets() {
    let boardClassText = { boardClass: this.props.boardClass, boardText: this.props.boardText, canRemove: this.props.canRemove }
    for (let i = 0; i < 9; i++) {
      let numbers = new Array(10);
      for (let k = 0; k < 10; k++) {
        numbers[k] = [];
      }
      for (let j = 0; j < 9; j++) {
        let temp = this.props.avail[i][j];
        for (let k = 0; k < temp.length; k++) {
          numbers[temp[k]].push({ i: i, j: j })
        }
      }
      for (let k = 1; k < 10; k++) {
        boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
        boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
      }
      for (let j = 0; j < 9; j++) {
        let numbers = new Array(10);
        for (let k = 0; k < 10; k++) {
          numbers[k] = [];
        }
        for (let i = 0; i < 9; i++) {
          let temp = this.props.avail[i][j];
          for (let k = 0; k < temp.length; k++) {
            numbers[temp[k]].push({ i: i, j: j })
          }
        }
        for (let k = 1; k < 10; k++) {
          boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
          boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
        }
        for (let boxRight = 0; boxRight < 3; boxRight++) {
          for (let boxDown = 0; boxDown < 3; boxDown++) {
            let numbers = new Array(10);
            for (let k = 0; k < 10; k++) {
              numbers[k] = [];
            }
            for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
              for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
                let temp = this.props.avail[i][j];
                for (let k = 0; k < temp.length; k++) {
                  numbers[temp[k]].push({ i: i, j: j })
                }
              }
            }
            for (let k = 1; k < 10; k++) {
              boardClassText = this.hiddenSubsets2helper(k, numbers, boardClassText);
              boardClassText = this.hiddenSubsets3helper(k, numbers, boardClassText);
            }
          }
        }
      }
    }
    this.props.SetState({ boardClass: boardClassText.boardClass, boardText: boardClassText.boardText, canRemove: boardClassText.canRemove });
  }
  sumArray(a, ...args) {
    let temp = [...a]
    args.forEach(arg => {
      arg.forEach(element => {
        if (temp.filter(e => { return (e.i === element.i && e.j === element.j) }).length === 0) temp.push(element);
      });
    })
    return temp
  }

  compare(a, b) {
    return JSON.stringify(a) === JSON.stringify(b)
  }
  secondvalues(a, b) {
    if (b.length < 2) return false;
    if (a.length === 2) {
      if (this.compare(a, b)) return false;
    }
    let temp = [...a];
    b.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    return temp.length < 4;
  }
  thirdvalue(a, b, c) {
    if (c.length < 2) return false;
    if (a.length === 2) {
      if (this.compare(a, b) || this.compare(a, c)) return false
    }
    if (b.length === 2) {
      if (this.compare(b, c)) return false
    }
    let temp = [...a];
    b.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    c.forEach(element => {
      if (!temp.includes(element)) temp.push(element);
    });
    return temp.length < 4;
  }
  Subsets2helper(a, b, others, boardClass, boardText) {
    let numbers = this.props.avail[a.i][a.j];
    let canRemove = [];
    others = others.filter(e => ((!(a.i === e.i && a.j === e.j)) || (!(b.i === e.i && b.j === e.j))))
    others.forEach(cell => {
      if (this.props.board[cell.i][cell.j] > 0) return;
      let removable = this.props.avail[cell.i][cell.j].filter((e) => (numbers.includes(e)));
      if (removable.length > 0) {
        boardText[cell.i][cell.j] = "avail";
        boardClass[cell.i][cell.j].push("canRemove")
      }
      removable.forEach(number => {
        canRemove.push({ i: cell.i, j: cell.j, value: number })
      })
    });
    boardClass[a.i][a.j].push("subset2");
    boardText[a.i][a.j] = this.props.avail[a.i][a.j].join(",");
    boardClass[b.i][b.j].push("subset2");
    boardText[b.i][b.j] = this.props.avail[b.i][b.j].join(",");
    return ({ boardClass, boardText, canRemove })
  }
  Subsets3helper(a, b, c, others, boardClass, boardText) {
    let numbers = this.sumArray(this.props.avail[a.i][a.j],
      this.props.avail[b.i][b.j],
      this.props.avail[c.i][c.j])
    let canRemove = [];
    others = others.filter(e => ((!(a.i === e.i && a.j === e.j)) || (!(b.i === e.i && b.j === e.j)) || (!(c.i === e.i && c.j === e.j))))
    others.forEach(cell => {
      if (this.props.board[cell.i][cell.j] > 0) return;
      let removable = this.props.avail[cell.i][cell.j].filter((e) => (numbers.includes(e)));
      if (removable.length > 0) {
        boardText[cell.i][cell.j] = "avail";
        boardClass[cell.i][cell.j].push("canRemove")
      }
      removable.forEach(number => {
        canRemove.push({ i: cell.i, j: cell.j, value: number })
      })
    });
    boardClass[a.i][a.j].push("subset3");
    boardText[a.i][a.j] = this.props.avail[a.i][a.j].join(",");
    boardClass[b.i][b.j].push("subset3");
    boardText[b.i][b.j] = this.props.avail[b.i][b.j].join(",");
    boardClass[c.i][c.j].push("subset3");
    boardText[c.i][c.j] = this.props.avail[c.i][c.j].join(",");
    return ({ boardClass, boardText, canRemove })
  }
  subsets() {
    let boardClass = this.props.boardClass;
    let boardText = this.props.boardText;
    let canRemove = [];
    for (let i = 0; i < 9; i++) {
      let others = [];
      for (let j = 0; j < 9; j++) {
        others.push({ i, j })
      }
      for (let j = 0; j < 9; j++) {
        if (this.props.board[i][j] < 1 && this.props.avail[i][j].length < 4 && this.props.avail[i][j].length > 1) {
          let array = this.props.avail[i][j];
          let secondvalues = [];
          let ib = i;
          for (let jb = j + 1; jb < 9; jb++) {
            if (this.props.board[ib][jb] < 1 && this.secondvalues(array, this.props.avail[ib][jb])) {
              secondvalues.forEach(value => {
                if (this.thirdvalue(array, value.avail, this.props.avail[ib][jb])) {
                  let r = this.Subsets3helper({ i, j }, { i: ib, j: jb }, { i: value.i, j: value.j }, others, boardClass, boardText)
                  r.canRemove.forEach(e => (canRemove.push(e)));
                  boardClass = r.boardClass;
                  boardText = r.boardText;

                  /*  boardClass[i][j].push("subset3");
                   boardText[i][j] = array.join(",");
                   boardClass[value.i][value.j].push("subset3");
                   boardText[value.i][value.j] = value.avail.join(",");
                   boardClass[ib][jb].push("subset3");
                   boardText[ib][jb] = this.state.avail[ib][jb].join(","); */
                }

              })
              secondvalues.push({ i: ib, j: jb, avail: this.props.avail[ib][jb] });
            }
          }
        }
      }
    }
    for (let j = 0; j < 9; j++) {
      let others = [];
      for (let i = 0; i < 9; i++) {
        others.push({ i, j })
      }
      for (let i = 0; i < 9; i++) {
        if (this.props.board[i][j] < 1 && this.props.avail[i][j].length < 4 && this.props.avail[i][j].length > 1) {
          let array = this.props.avail[i][j];
          let secondvalues = [];
          let jb = j;
          for (let ib = i + 1; ib < 9; ib++) {
            if (this.props.board[ib][jb] < 1 && this.secondvalues(array, this.props.avail[ib][jb])) {
              secondvalues.forEach(value => {
                if (this.thirdvalue(array, value.avail, this.props.avail[ib][jb])) {
                  let r = this.Subsets3helper({ i, j }, { i: ib, j: jb }, { i: value.i, j: value.j }, others, boardClass, boardText)
                  r.canRemove.forEach(e => (canRemove.push(e)));
                  boardClass = r.boardClass;
                  boardText = r.boardText;
                  /* boardClass[i][j].push("subset3");
                  boardText[i][j] = array.join(",");
                  boardClass[value.i][value.j].push("subset3");
                  boardText[value.i][value.j] = value.avail.join(",");
                  boardClass[ib][jb].push("subset3");
                  boardText[ib][jb] = this.state.avail[ib][jb].join(","); */
                }
              })
              secondvalues.push({ i: ib, j: jb, avail: this.props.avail[ib][jb] });
            }
          }
        }
      }
    }
    for (let boxRight = 0; boxRight < 3; boxRight++) {
      for (let boxDown = 0; boxDown < 3; boxDown++) {
        let others = [];
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
            others.push({ i, j })
          }
        }
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
            if (this.props.board[i][j] < 1 && this.props.avail[i][j].length < 4 && this.props.avail[i][j].length > 1) {
              let array = this.props.avail[i][j];
              let secondvalues = [];
              for (let ib = boxRight * 3; ib < (boxRight + 1) * 3; ib++) {
                for (let jb = boxDown * 3; jb < (boxDown + 1) * 3; jb++) {
                  if (this.props.board[ib][jb] < 1 && (ib !== i || jb !== j) && this.secondvalues(array, this.props.avail[ib][jb])) {
                    secondvalues.forEach(value => {
                      if (this.thirdvalue(array, value.avail, this.props.avail[ib][jb])) {
                        let r = this.Subsets3helper({ i, j }, { i: ib, j: jb }, { i: value.i, j: value.j }, others, boardClass, boardText)
                        r.canRemove.forEach(e => (canRemove.push(e)));
                        boardClass = r.boardClass;
                        boardText = r.boardText;
                        /*  boardClass[i][j].push("subset3");
                         boardText[i][j] = array.join(",");
                         boardClass[value.i][value.j].push("subset3");
                         boardText[value.i][value.j] = value.avail.join(",");
                         boardClass[ib][jb].push("subset3");
                         boardText[ib][jb] = this.state.avail[ib][jb].join(","); */
                      }
                    })
                    secondvalues.push({ i: ib, j: jb, avail: this.props.avail[ib][jb] });
                  }
                }
              }
            }
          }
        }
      }
    }
    for (let i = 0; i < 9; i++) {
      let others = [];
      for (let j = 0; j < 9; j++) {
        others.push({ i, j })
      }
      for (let j = 0; j < 9; j++) {
        let ib = i;
        if (this.props.board[i][j] < 1 && this.props.avail[i][j].length === 2) {
          for (let jb = j + 1; jb < 9; jb++) {
            if (this.props.board[ib][jb] < 1) {
              if (this.compare(this.props.avail[ib][jb], this.props.avail[i][j])) {

                let r = this.Subsets2helper({ i, j }, { i: ib, j: jb }, others, boardClass, boardText);
                r.canRemove.forEach(e => (canRemove.push(e)));
                boardClass = r.boardClass;
                boardText = r.boardText;

                /* boardClass[i][j].push("subset2");
                boardText[i][j] = this.state.avail[i][j].join(",");
                boardClass[ib][jb].push("subset2");
                boardText[ib][jb] = this.state.avail[i][j].join(","); */
              }
            }
          }
        }
      }
    }
    for (let j = 0; j < 9; j++) {
      let others = [];
      for (let i = 0; i < 9; i++) {
        others.push({ i, j })
      }
      for (let i = 0; i < 9; i++) {
        let jb = j;
        if (this.props.board[i][j] < 1 && this.props.avail[i][j].length === 2) {
          for (let ib = i + 1; ib < 9; ib++) {
            if (this.props.board[ib][jb] < 1) {
              if (this.compare(this.props.avail[ib][jb], this.props.avail[i][j])) {
                let r = this.Subsets2helper({ i, j }, { i: ib, j: jb }, others, boardClass, boardText);
                r.canRemove.forEach(e => (canRemove.push(e)));
                boardClass = r.boardClass;
                boardText = r.boardText;

                /*  boardClass[i][j].push("subset2");
                 boardText[i][j] = this.state.avail[i][j].join(",");
                 boardClass[ib][jb].push("subset2");
                 boardText[ib][jb] = this.state.avail[i][j].join(","); */
              }
            }
          }
        }
      }
    }
    for (let boxRight = 0; boxRight < 3; boxRight++) {
      for (let boxDown = 0; boxDown < 3; boxDown++) {
        let others = [];
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
            others.push({ i, j })
          }
        }
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
            if (this.props.board[i][j] < 1 && this.props.avail[i][j].length === 2) {
              for (let ib = boxRight * 3; ib < (boxRight + 1) * 3; ib++) {
                for (let jb = boxDown * 3; jb < (boxDown + 1) * 3; jb++) {
                  if (this.props.board[ib][jb] < 1) {
                    if ((i !== ib || j !== jb) && this.compare(this.props.avail[ib][jb], this.props.avail[i][j])) {
                      let r = this.Subsets2helper({ i, j }, { i: ib, j: jb }, others, boardClass, boardText);
                      r.canRemove.forEach(e => (canRemove.push(e)));
                      boardClass = r.boardClass;
                      boardText = r.boardText;

                      /*   boardClass[i][j].push("subset2");
                        boardText[i][j] = this.state.avail[i][j].join(",");
                        boardClass[ib][jb].push("subset2");
                        boardText[ib][jb] = this.state.avail[i][j].join(","); */
                    }
                  }

                }
              }
            }
          }
        }
      }
    }
    this.props.SetState({ boardClass, boardText, canRemove });
  }
  uniques() {
    let boardText = this.props.boardText;
    let boardClass = this.props.boardClass;
    for (let k = 1; k < 10; k++) {
      let pos = { i: 0, j: 0 };
      for (let j = 0; j < 9; j++) {
        let count = 0;
        for (let i = 0; i < 9; i++) {
          if (this.props.avail[i][j].includes(k)) {
            pos.i = i;
            pos.j = j;
            count++;
          }
          if (count > 1) break;
        }
        if (count === 1 && this.props.avail[pos.i][pos.j].length !== 1) {
          boardClass[pos.i][pos.j].push("unique");
          boardText[pos.i][pos.j] = k;
        }
      }
      for (let i = 0; i < 9; i++) {
        let count = 0;
        for (let j = 0; j < 9; j++) {
          if (this.props.avail[i][j].includes(k)) {
            pos.i = i;
            pos.j = j;
            count++;
          }
          if (count > 1) break;
        }
        if (count === 1 && this.props.avail[pos.i][pos.j].length !== 1) {
          boardClass[pos.i][pos.j].push("unique");
          boardText[pos.i][pos.j] = k;
        }
      }
      for (let boxRight = 0; boxRight < 3; boxRight++) {
        for (let boxDown = 0; boxDown < 3; boxDown++) {
          let count = 0;
          for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
            for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
              if (this.props.avail[i][j].includes(k)) {
                count++;
                pos.i = i;
                pos.j = j;
              }
              if (count > 1) break;
            }
            if (count > 1) break;
          }
          if (count === 1 && this.props.avail[pos.i][pos.j].length !== 1) {
            boardClass[pos.i][pos.j].push("unique");
            boardText[pos.i][pos.j] = k;
          }
        }
      }
    }
    this.props.SetState({ boardClass, boardText });
  }
  setRule(rule) {
    this.props.clear();
    switch (rule) {
      case "Singles":
        this.singles();
        break;
      case "Uniques":
        this.uniques();
        break;
      case "Subsets":
        this.subsets();
        break;
      case "Hidden":
        this.hiddenSubsets();
        break;
      case "XWing":
        this.xwing();
        break
      default:
    }
    this.props.SetState({ rule });
  }

  render() {
    return (<div key="Strategies">
      <div className={(this.props.Strategies) ? "" : "none"}>
        <button id="singlesBtn" onClick={() => { this.setRule("Singles") }} className={"btn-sm highlightBtns " + (this.props.rule !== "Singles" ? "" : "bg-secondary")}>Singles</button>
        <button id="uniquesBtn" onClick={() => { this.setRule("Uniques") }} className={"btn-sm highlightBtns " + (this.props.rule !== "Uniques" ? "" : "bg-secondary")}>Uniques</button>
        <button id="subsetsBtn" onClick={() => { this.setRule("Subsets") }} className={"btn-sm highlightBtns " + (this.props.rule !== "Subsets" ? "" : "bg-secondary")}>Subsets</button>
        <button id="hiddenBtn" onClick={() => { this.setRule("Hidden") }} className={"btn-sm highlightBtns " + (this.props.rule !== "Hidden" ? "" : "bg-secondary")}>Hidden</button>
        <button id="hiddenBtn" onClick={() => { this.setRule("XWing") }} className={"btn-sm highlightBtns " + (this.props.rule !== "XWing" ? "" : "bg-secondary")}>XWing</button>
      </div>

    </div>)
  }

}