import React, { Component } from 'react';
import Strategies from './Strategies.js';
export default class Board extends Component {

  componentDidMount() {
    this.allavail();
  }
  allavail() {
    this.clear(false);
    let win = true;
    for (let i = 0; i < 9; i++) {
      for (let j = 0; j < 9; j++) {
        if (this.props.board[i][j] < 1) win = false;
      }
    }
    if (win === true) {
      this.props.SetState({ screen: "Win" });
      return;
    }
    const avail = []
    var fixedAvail = [];
    if (this.props.AN) {
      for (let i = 0; i < 9; i++) {
        avail.push([])
        for (let j = 0; j < 9; j++) {
          avail[i].push([])
          if (this.props.board[i][j] < 1) {
            const array = [];
            for (let k = 1; k < 10; k++) {
              if (this.checkall(k, i, j)) {
                array.push(k);
              }
            }
            avail[i][j] = array;
          }
        }
      }
      avail.forEach(e => {
        let array = [];
        e.forEach(r => {
          array.push([...r])
        })
        fixedAvail.push(array);
      })
      this.props.removed.forEach(value => {
        if (this.props.board[value.i][value.j] < 1) {
          avail[value.i][value.j] = avail[value.i][value.j].filter(e => {
            return (e * 1 !== 1 * value.value);
          });
        }
      })
    } else {
      for (let i = 0; i < 9; i++) {
        avail.push([])
        for (let j = 0; j < 9; j++) {
          avail[i].push([])
          if (this.props.board[i][j] < 1) {
            const array = [];
            for (let k = 1; k < 10; k++) {
              array.push(k);
            }
            avail[i][j] = array;
          }
        }
      }
      avail.forEach(e => {
        let array = [];
        e.forEach(r => {
          array.push([...r])
        })
        fixedAvail.push(array);
      })
      this.props.removed.forEach(value => {
        if (this.props.board[value.i][value.j] < 1) {
          avail[value.i][value.j] = avail[value.i][value.j].filter(e => {
            return (e * 1 !== 1 * value.value);
          });
        }
      })

    }

    this.props.SetState({ avail, fixedAvail }, () => {
      if (!this.refs.Strategies) return;
      switch (this.props.rule) {
        case "Uniques": this.refs.Strategies.uniques(); break;
        case "Singles": this.refs.Strategies.singles(); break;
        case "Subsets": this.refs.Strategies.subsets(); break;
        case "Hidden": this.refs.Strategies.hiddenSubsets(); break;
        case "XWing": this.refs.Strategies.xwing(); break;
        default:
      }
    });

  }
  checkrow(input, j) {
    var board = this.props.board;
    for (var i = 0; i < 9; i++) {
      if (board[i][j] === input) return false;
    }
    return true;
  }
  checkcolumn(input, i) {
    var board = this.props.board;
    for (var j = 0; j < 9; j++) {
      if (board[i][j] === input) return false;
    }
    return true;
  }
  checkbox(input, i, j) {
    var boxRight = Math.floor(i / 3)
    var boxDown = Math.floor(j / 3)
    var board = this.props.board;
    for (i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
      for (j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
        if (board[i][j] === input) return false;
      }
    }
    return true;
  }
  checkall(input, i, j) {
    input = 1 * input;
    return (this.checkbox(input, i, j) && this.checkrow(input, j) && this.checkcolumn(input, i));
  }
  setTab(tab) {
    this.props.SetState({ tab });
  }
  removeValid() {
    this.props.SetState({ remove: !this.props.remove })
  }
  erase() {
    let board = this.props.board;
    board[this.props.selected.i][this.props.selected.j] = this.props.fixedBoard[this.props.selected.i][this.props.selected.j];
    this.props.SetState({ board, selected: { i: -1, j: -1 } }, () => { this.allavail() })
  }
  handleKeyPress = (event) => {
    let valid = ["1", "2", "3", "4", "5", "6", "7", "8", "9"];
    let i = this.props.selected.i;
    if (i === -1) return;
    let j = this.props.selected.j
    if (valid.includes(event.key) && this.props.fixedAvail[i][j].includes(event.key * 1)) {
      if (this.props.remove === true) {
        let removed = this.props.removed;
        let temp = removed.filter(e => { return e.i === i && e.j === j && e.value === event.key });
        if (temp.length > 0) {
          removed.splice(removed.indexOf(temp[0]), 1);
          this.props.SetState({ removed }, () => { this.allavail() });
        } else if (this.props.avail[i][j].length > 1) {
          removed.push({ i, j, value: event.key })
          this.props.SetState({ removed }, () => { this.allavail() });
        }
      } else if (this.props.avail[i][j].includes(event.key * 1)) {
        var board = this.props.board;
        board[i][j] = 1 * event.key;
        this.props.SetState({ board, selected: { i: -1, j: -1 } }, () => { this.allavail() });
      }
    }
  }
  reset() {
    this.clear();
    let board = [];
    this.props.fixedBoard.forEach(array => {
      board.push([...array]);
    })
    this.props.SetState({
      board,
      removed: [],
      rule: ""
    }, () => (this.allavail()));
  }
  click(i, j) {
    if (this.props.fixedBoard[i][j] > 0) {
      return;
    }
    var board = this.props.board;
    this.props.SetState({ board: board, selected: { i, j } }, () => (this.allavail()));
  }
  clear(keepSelected) {
    let boardClass = this.props.boardClass;
    let boardText = this.props.boardText;
    let selected = this.props.selected;
    for (let j = 0; j < 9; j++) {
      for (let i = 0; i < 9; i++) {
        boardClass[i][j] = [];
        boardText[i][j] = "";
      }
    }
    if (keepSelected !== false && this.props.selected.i !== -1) {
      selected = { i: -1, j: -1 };
    }
    this.props.SetState({ boardClass, boardText, selected });
  }
  renderBoard() {
    const board = [];
    let availableNumbers = [];
    let columnNumbers = [];
    let rowNumbers = [];
    let boxNumbers = [];
    let boardClass = this.props.boardClass;
    let boardText = this.props.boardText;

    for (let j = 0; j < 9; j++) {
      const row = [];
      for (let i = 0; i < 9; i++) {
        let cls = [...boardClass[i][j]];
        let txt = boardText[i][j];
        let s = false;
        if (this.props.selected.i === i && this.props.selected.j === j) {
          cls = ["selected"];
          txt = ""
          s = true;
        }
        if (this.props.fixedBoard[i][j] > 0) {

        } else if (this.props.board[i][j] > 0) {
          cls.push("text-primary")
        }
        if (txt === "avail") {
          let count = -1;
          txt = this.props.avail[i][j].map((entry, index) => {
            if ((this.props.canRemove.filter((e) => (e.i === i && e.j === j && e.value * 1 === 1 * entry)).length === 0)) return <span></span>
            count++;
            return <span>{(count > 0 ? "," : "")}<span className={"text-danger"}>{entry}</span></span>
          });
        }
        row.push(<div key={"cell:" + i + "," + j} id={"cell:" + i + "," + j} className={cls.join(" ")} onClick={() => this.click(i, j)}
          style={{
            "verticalAlign": "top",
            "textAlign": "center",
            "borderRightWidth": (((i % 3) === 2) ? 3 : 0) + 1 + "px",
            "borderLeftWidth": ((i === 0) ? 3 : 0) + 1 + "px",
            "borderBottomWidth": (((j % 3) === 2) ? 3 : 0) + 1 + "px",
            "borderTopWidth": ((j === 0) ? 3 : 0) + 1 + "px",
            "borderStyle": "solid", height: 30 + "px", width: 30 + "px", display: "inline-block", borderColor: "black"
          }}>{(this.props.board[i][j] > 0 && !s) ? this.props.board[i][j] : txt}</div>);
      }
      board.push(<div key={"row:" + j}>{row}</div>);
    }
    if (this.props.selected.i !== -1) {
      let i = this.props.selected.i;
      let j = this.props.selected.j;
      let removed = this.props.removed.filter(r => { return r.i === i && r.j === j })
      let first = true;
      this.props.fixedAvail[i][j].forEach(e => {
        if (!first) availableNumbers.push(<span>,</span>);
        first = false;
        if (removed.filter(r => { return r.value * 1 === 1 * e }).length > 0) {
          availableNumbers.push(<span className="removedNumber">{e}</span>);

        } else {
          availableNumbers.push(<span>{e}</span>);
        }
      })
      for (let j = 0; j < 9; j++) {
        let numbers = "";

        if (this.props.board[i][j]) {
          numbers = "Set to " + this.props.board[i][j];
        } else {
          numbers = this.props.avail[i][j].toString();
        }
        numbers = <div>{numbers}</div>;
        columnNumbers.push(numbers);
      }
      for (let i = 0; i < 9; i++) {
        let numbers = "";
        if (this.props.board[i][j]) {
          numbers = "Set to " + this.props.board[i][j];
        } else {
          numbers = this.props.avail[i][j].toString();
        }
        numbers = <div>{numbers}</div>;
        rowNumbers.push(numbers);
      }
      var boxRight = Math.floor(i / 3)
      var boxDown = Math.floor(j / 3)
      for (let j = boxDown * 3; j < (boxDown + 1) * 3; j++) {
        for (let i = boxRight * 3; i < (boxRight + 1) * 3; i++) {
          let numbers = "";
          if (this.props.board[i][j]) {
            numbers = "Set to " + this.props.board[i][j];
          } else {
            numbers = this.props.avail[i][j].toString();
          }
          numbers = <div>{numbers}</div>;
          boxNumbers.push(numbers);
        }
      }
    } else {
      availableNumbers = [];
      columnNumbers = [];
      rowNumbers = [];
      boxNumbers = [];
    }
    columnNumbers = <div>{columnNumbers}</div>;

    return (
      <div tabIndex="0" key="Board" onKeyPress={this.handleKeyPress}>
        <div className="d-flex justify-content-center">
          <div>
            <div><h1>Sudoku Solver</h1></div>
            <div>Difficulty: {this.props.difficulty}<span style={{ "float": "right", "marginRight": "20px" }}>Type: {this.props.type}</span></div>
            <div style={{ "paddingTop": "5px" }}>Available numbers done for you: {this.props.AN ? "Yes" : "No"}</div>
            <div style={{ "paddingTop": "5px", "paddingBottom": "5px" }}>Strategies: {this.props.Strategies ? "On" : "Off"}</div>
            <div className="board">{board}</div>
            <Strategies
              ref="Strategies"
              board={this.props.board}
              avail={this.props.avail}
              allavail={this.allavail.bind(this)}
              boardClass={this.props.boardClass}
              boardText={this.props.boardText}
              SetState={this.props.SetState}
              canRemove={this.props.canRemove}
              Strategies={this.props.Strategies}
              clear={this.props.clear}


            ></Strategies>
            <div className="d-flex justify-content-center mt-2">
              <button onClick={() => { this.reset() }} className="btn-sm mr-2" >Reset</button>
              <button onClick={() => { this.reset(); this.props.setScreen("Menu") }} className="btn-sm mr-2" >New Game</button>
            </div>
          </div>
        </div>
        <div className="d-flex justify-content-center">
          <div id="availnums" className={"d-flex flex-column " + ((this.props.selected.i !== -1) ? "" : "fade")}>
            <div className="avail pr-2">
              <div className="availnums pb-2"><div>Valid numbers for selected cell</div>
                {(this.props.selected.i !== -1) && this.props.fixedAvail[this.props.selected.i][this.props.selected.j].map((number, index) => (
                  <button onClick={() => { this.handleKeyPress({ key: "" + number }) }}
                    className={
                      "btn-sm " + ((this.props.removed.filter((e) => (e.i === this.props.selected.i && e.j === this.props.selected.j && e.value * 1 === 1 * number)).length > 0) ? "text-danger" : "")}>{number}</button>
                ))}


              </div>
              <div className="float-right">
                <button onClick={() => (this.erase())} className={"btn-sm "} >Erase</button>
                <button id="removeButton" onClick={() => (this.removeValid())} className={"btn-sm " + (this.props.remove ? "bg-danger" : "")} >Remove</button>
              </div>
            </div>
            <div className="mt-2">
              <button onClick={() => { this.setTab("Row") }} className={"btn-sm mr-2 " + (this.props.tab === "Row" ? "bg-secondary" : "")}>Row</button>
              <button onClick={() => { this.setTab("Column") }} className={"btn-sm mr-2 " + (this.props.tab === "Column" ? "bg-secondary" : "")}>Column</button>
              <button onClick={() => { this.setTab("Box") }} className={"btn-sm " + (this.props.tab === "Box" ? "bg-secondary" : "")}>Box</button>
            </div>
            <div className="avail">
              <div id="row" className={"availbox " + (this.props.tab === "Row" ? "" : "none")}>
                <div>Valid numbers for cells in row</div>
                {rowNumbers}
              </div>
              <div id="column" className={"availbox " + (this.props.tab === "Column" ? "" : "none")}><div>Valid numbers for cells in column</div>
                {columnNumbers}
              </div>
              <div id="box" className={"availbox " + (this.props.tab === "Box" ? "" : "none")}>
                <div>Valid numbers for cells in box</div>
                {boxNumbers}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };
  render() {
    return (this.renderBoard())
  }
}